import { Dispatch, useState } from "react";
import { Modal, OverlayTrigger, Tooltip } from "react-bootstrap";
import bet from "../api/bet";
import { Round } from "../api/getPaginatedRounds";
import { TierConfig, TierConfigs } from "../api/getTierConfigs";
import { UserBets } from "../api/getUserBets";
import withdrawl from "../api/withdrawl";
import constants from "../constants";
import { IClientState } from "../context/ClientContext";
import getCorrectContract from "../utils/getCorrectContract";
import getCorrectTokenContract from "../utils/getCorrectTokenContract";
import getRandomInt from "../utils/getRandomInt";
import { errorNotification, successNotification } from "../utils/notifications";

export default ({
    menu,
    client,
    viewkey,
    potNumber,
    round,
    pollConfig,
    currentRoundsUserBets,
    getSSCRTBalance, 
    getSEFIBalance,
    triggerGetPaginatedUserBets,
    getCurrentRoundsStateTrigger
}: {
    menu: string,
    client: IClientState | null,
    viewkey: String | null,
    potNumber: number,
    round: Round,
    pollConfig: TierConfig,
    currentRoundsUserBets: UserBets | null,
    getSSCRTBalance: Function,
    getSEFIBalance: Function,
    triggerGetPaginatedUserBets: Function,
    getCurrentRoundsStateTrigger: Function
}) => {
    const [buttonLoading, setButtonLoading] = useState<{ pot: number, round: number } | null>(null)
    const [enterPotModal, setEnterPotModal] = useState<IEnterPotModal>({
        show: false,
        data: null
    })
    return (
        <div className="col round-container">
            <div className="row">
                <div className="col" style={{ fontSize: "1.25rem", marginBottom: "5px" }}>
                    Round {round.round_number} -
                    {
                        round.round_end_timestamp && round.round_end_pool_size ?
                            <span style={{ marginLeft: "5px" }} className="badge badge-secondary">Finished</span> :
                            <span style={{ marginLeft: "5px" }} className="badge badge-success">Open</span>
                    }
                </div>
            </div>
            {
                round.round_end_timestamp && round.round_end_pool_size && round.lucky_number &&
                <div className="row" >
                    <div className="col" style={{ fontSize: "1.5rem", marginBottom: "5px" }}>
                        {"Lucky Number: " + round.lucky_number}
                    </div>
                </div>
            }
            <div className="row">
                <div className="col" style={{ marginBottom: "5px" }}>
                    {
                        round.round_end_timestamp && round.round_end_pool_size ?
                            "Triggered at " + new Date(round.round_end_timestamp * 1000).toLocaleString() :
                            potNumber === 1 ? "Trigger every day" :
                                potNumber === 2 ? "Trigger every hour" :
                                    potNumber === 3 && "Trigger every 5m"
                    }
                </div>
            </div>
            <div className="row">
                <div className="col">
                    <span style={{ fontSize: "3rem" }}>
                        {
                            round.round_end_timestamp && round.round_end_pool_size ?
                                `${parseInt(round.round_end_pool_size) / 1000000}` :
                                `${parseInt(round.pool_size) / 1000000}`
                        }
                    </span>
                    <br />
                    {
                        round.round_end_timestamp && round.round_end_pool_size ?
                            menu + " Final Pot Size" :
                            menu + " Pot Size"
                    }

                </div>
                <div className="col">
                    <div style={{ margin: "5px" }}>
                        {
                            round.users_count >= pollConfig.min_entries ?
                            <div><span style={{color: "#5cb85c", fontWeight: "bold"}}>{round.users_count}</span> Participants</div> :
                            <div><span style={{color: "#f0ad4e", fontWeight: "bold"}}>{pollConfig.min_entries - round.users_count}</span> Participants left</div> 
                        }
                    </div>
                    {
                        round.winner_users_count !== null && <div style={{ margin: "5px" }}>{round.winner_users_count} {round.winner_users_count === 1 ? "Winner" : "Winners"}</div>
                    }
                    {
                        currentRoundsUserBets && currentRoundsUserBets.user_bets.find((bet) => bet.tier === potNumber && bet.round_number === round.round_number && !bet.claimed_reward && !round.round_end_timestamp) &&
                        <button type="button" className="btn btn-danger" style={{ borderRadius: "10px", margin: "5px" }}
                            onClick={async () => {
                                try {
                                    if (!client || !viewkey) return
                                    setButtonLoading({ pot: potNumber, round: round.round_number })
                                    const contract = getCorrectContract(menu)
                                    await withdrawl(client, contract, potNumber, round.round_number)
                                    await getCurrentRoundsStateTrigger(client, viewkey, true)
                                    await triggerGetPaginatedUserBets()
                                    if (menu === "sSCRT") await getSSCRTBalance()
                                    if (menu === "SEFI") await getSEFIBalance()
                                    setButtonLoading(null)
                                    successNotification("Cancel Bet Success!")
                                } catch (e) {
                                    setButtonLoading(null)
                                    errorNotification(e)
                                }
                            }}
                        >
                            {`${parseInt(pollConfig.entry_fee) / 1000000} ${menu}`}
                            <br />
                            {
                                buttonLoading && buttonLoading.pot === potNumber && buttonLoading.round === round.round_number ?
                                    <i className="fa fa-spinner fa-spin"></i>
                                    : "Cancel Bet"
                            }
                        </button>
                    }
                    {
                        currentRoundsUserBets && currentRoundsUserBets.user_bets.find((bet) => bet.tier === potNumber && bet.round_number === round.round_number && !bet.claimed_reward && round.round_end_timestamp && bet.number === round.lucky_number) &&
                        <button type="button" className="btn btn-success" style={{ borderRadius: "10px", margin: "5px" }}
                            onClick={async () => {
                                try {
                                    if (!client || !viewkey) return
                                    setButtonLoading({ pot: potNumber, round: round.round_number })
                                    const contract = getCorrectContract(menu)
                                    await withdrawl(client, contract, potNumber, round.round_number)
                                    await getCurrentRoundsStateTrigger(client, viewkey, true)
                                    if (menu === "sSCRT") await getSSCRTBalance()
                                    if (menu === "SEFI") await getSEFIBalance()
                                    await triggerGetPaginatedUserBets()
                                    setButtonLoading(null)
                                    successNotification("Withdraw Winnings Success!")
                                } catch (e) {
                                    setButtonLoading(null)
                                    errorNotification(e)
                                }
                            }}
                        >
                            {
                                round.round_end_pool_size && round.winner_users_count ? (parseInt(round.round_end_pool_size) / round.winner_users_count) / 1000000 + " "+ menu : "..."
                            }
                            <br />
                            {
                                buttonLoading && buttonLoading.pot === potNumber && buttonLoading.round === round.round_number ?
                                    <i className="fa fa-spinner fa-spin"></i>
                                    : "Claim"
                            }
                        </button>
                    }
                    {
                        currentRoundsUserBets && !currentRoundsUserBets.user_bets.find((bet) => bet.tier === potNumber && bet.round_number === round.round_number) && !round.lucky_number &&
                        <button type="button" className="btn btn-info" style={{ borderRadius: "10px", margin: "5px" }}
                            onClick={() => setEnterPotModal({
                                show: true,
                                data: {
                                    pot: {
                                        number: potNumber,
                                        configs: pollConfig
                                    },
                                    round: round
                                }
                            })}
                        >
                            {`${parseInt(pollConfig.entry_fee) / 1000000} ${menu}`}
                            <br />
                            Enter Pot
                        </button>
                    }
                    {
                        currentRoundsUserBets && currentRoundsUserBets.user_bets.find((bet) => bet.tier === potNumber && bet.round_number === round.round_number && round.round_end_timestamp && bet.number !== round.lucky_number) &&
                        <button disabled type="button" className="btn btn-danger" style={{ borderRadius: "10px", margin: "5px" }}>
                            You Lost
                        </button>
                    }
                    {
                        currentRoundsUserBets && currentRoundsUserBets.user_bets.find((bet) => bet.tier === potNumber && bet.round_number === round.round_number && bet.claimed_reward && round.round_end_timestamp) &&
                        <button disabled type="button" className="btn btn-secondary" style={{ borderRadius: "10px", margin: "5px" }}>
                            {
                                round.round_end_pool_size && round.winner_users_count ? (parseInt(round.round_end_pool_size) / round.winner_users_count) / 1000000 + " " + menu : "..."
                            }
                            <br />
                            Claimed
                        </button>
                    }
                    {
                        currentRoundsUserBets && !currentRoundsUserBets.user_bets.find((bet) => bet.tier === potNumber && bet.round_number === round.round_number) && round.lucky_number &&
                        <button type="button" className="btn btn-secondary" style={{ borderRadius: "10px", margin: "5px" }}>
                            Finished
                        </button>
                    }

                </div>
            </div>
            {
                client && viewkey &&
                <EnterPotModal
                    menu={menu}
                    client={client}
                    viewkey={viewkey}
                    getCurrentRoundsStateTrigger={getCurrentRoundsStateTrigger}
                    getSSCRTBalance={getSSCRTBalance}
                    getSEFIBalance={getSEFIBalance}
                    enterPotModal={enterPotModal}
                    setEnterPotModal={setEnterPotModal}
                    triggerGetPaginatedUserBets={triggerGetPaginatedUserBets}
                />
            }
        </div>
    )
}


const EnterPotModal = ({
    menu,
    client,
    viewkey,
    getCurrentRoundsStateTrigger,
    getSSCRTBalance,
    getSEFIBalance,
    enterPotModal,
    setEnterPotModal,
    triggerGetPaginatedUserBets
}: {
    menu: string,
    client: IClientState,
    viewkey: String,
    getCurrentRoundsStateTrigger: Function,
    getSSCRTBalance: Function,
    getSEFIBalance: Function,
    enterPotModal: IEnterPotModal,
    setEnterPotModal: Dispatch<IEnterPotModal>,
    triggerGetPaginatedUserBets: Function
}) => {
    const [number, setNumber] = useState<number | null>(null)
    const [betLoading, setBetLoading] = useState(false)

    return (
        <Modal centered show={enterPotModal.show} onHide={() => setEnterPotModal({ show: false, data: null })} >
                <div style={{ background: "linear-gradient(180deg, #242525 0%, #000 180%)", color: "white" }}>
                    <div className="modal-header">
                        <h5 className="modal-title">Enter Pot - Choose a Number to Bet</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close" style={{ color: "white" }} onClick={() => setEnterPotModal({ show: false, data: null })}>
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <div className="row">
                            {
                                enterPotModal.data && Array.from(Array(enterPotModal.data.pot.configs.max_rand_number).keys()).map((value, i) =>
                                    <div className="col" style={{textAlign:"center"}}>
                                        <button type="button" style={{ width: "50px", margin: "5px" }} className={`btn btn-${number && number === i + 1 ? "success" : "secondary"}`} onClick={() => setNumber(i + 1)}>
                                            {i + 1}
                                        </button>
                                    </div>
                                )
                            }
                        </div>
                    </div>
                    <div className="modal-footer">
                    <OverlayTrigger
                        placement="right"
                        delay={{ show: 250, hide: 400 }}
                        overlay={(props: any) => (
                            <Tooltip id="button-tooltip" {...props} >
                                Pick a random number
                            </Tooltip>)}
                    >
                         <button className="mr-auto btn btn-primary py-2 px-4" type="button"
                         onClick={() => {
                             if(!enterPotModal.data) return
                            setNumber(getRandomInt(1,enterPotModal.data.pot.configs.max_rand_number))
                         }}> 
                            <i className="fas fa-question"></i>
                        </button>
                    </OverlayTrigger>
                       
                        <button
                            disabled={(number && !betLoading) ? false : true}
                            type="button" className="btn btn-success py-2 px-4" data-bs-dismiss="modal" onClick={async () => {
                                try {
                                    if (!enterPotModal.data || !number) return
                                    setBetLoading(true)
                                    const token_contract = getCorrectTokenContract(menu)
                                    const contract = getCorrectContract(menu)
                                    await bet(client, token_contract, contract, enterPotModal.data.pot.number, number, enterPotModal.data.pot.configs.entry_fee)
                                    await getCurrentRoundsStateTrigger(client, viewkey, true)
                                    if (menu === "sSCRT") await getSSCRTBalance()
                                    if (menu === "SEFI") await getSEFIBalance()
                                    await triggerGetPaginatedUserBets()
                                    setBetLoading(false)
                                    setNumber(null)
                                    setEnterPotModal({ show: false, data: null })
                                    successNotification("Bet Success!")
                                } catch (e) {
                                    setBetLoading(false)
                                    setNumber(null)
                                    errorNotification(e)
                                }
                            }}>
                            {
                                !betLoading ? "Bet" : <i className="fa fa-spinner fa-spin"></i>
                            }
                        </button>
                        <button type="button" className="btn btn-secondary py-2 px-4" data-bs-dismiss="modal" onClick={() => setEnterPotModal({ show: false, data: null })}>Close</button>
                    </div>
                </div>
        </Modal>
    )
}

interface IEnterPotModal {
    show: boolean,
    data: {
        pot: {
            number: number,
            configs: TierConfig
        },
        round: Round
    } | null
}